<script>
export default {
  data() {
    return {
      categories: this.getCategoryList(),
      programs: this.getProgramList(),
      categoryCurrentPage: 1,
      categoryPerPage: 2,
      programCurrentPage: 1,
      programPerPage: 2,
    };
  },
  computed: {
    categoryRows() {
      return this.categories.length;
    },
    programRows() {
      return this.programs.length;
    },
  },
  methods: {
    getCategoryList() {
      let categories = [];
      for (let i = 1; i <= 6; i++) {
        let categoryData = {
          category_no: i,
          category_name: "カテゴリ" + i,
          category_img_path: require("@/assets/logo.svg"),
        };
        categories.push(categoryData);
      }
      return categories;
    },

    getProgramList() {
      let programs = [];
      for (let i = 1; i <= 9; i++) {
        let programData = {
          program_no: i,
          program_name: "プログラムA",
          consumption_points: "200P",
          program_img_path: require("@/assets/logo.svg"),
        };
        programs.push(programData);
      }
      return programs;
    },

    gotoCategoryEdit() {
      this.$router.push({ name: "point_program_category_addEdit" });
    },

    gotoProgramEdit() {
      this.$router.push({ name: "point_program_addEdit" });
    },
  },
};
</script>

